        initialize: async function(options){
            this.styles = styles;
            this.domain = (options && options.domain) || ''; // Set domain to fetch svg / image assets from...

            $("html").attr("lang", language);

            $("#vlab").css("height", $(document).height());

            this.finishedLoading = false;

            var logger = postal.channel("logger");
            if (Unknown.unknownString) {
                logger.publish("vlabUnknownsSet", {unknownString: Unknown.unknownString});
            }

            var appChannel = postal.channel("app");
            var subscriptions = [
                appChannel.subscribe("finishedLoadingRequest", function(){
                    appChannel.publish("finishedLoading", {finishedLoading: this.isFinishedLoading()});
                }.bind(this)),
                appChannel.subscribe("selectedVesselRequest", function(){
                    appChannel.publish("selectedVessel", {selectedVessel: this.getSelectedVessel()});
                }.bind(this)),
                appChannel.subscribe("workbenchItemsRequest", function(){
                    appChannel.publish("workbenchItems", {workbenchItems: this.getWorkbenchItems()});
                }.bind(this)),
                appChannel.subscribe("loadAssignment", function(){
                    console.log("received external loadAssignment request");
                    this.loadAssignment();
                }.bind(this)),
                appChannel.subscribe("loadAssignmentJSON", function(data){
                    console.log("received external loadAssignmentJSON request");
                    this.loadAssignmentJSON(data);
                }.bind(this))
            ];

            // Set up the containers and app structure
            this.render();

            //$(window).resize(this.adjustColumns);
            $(window).resize(this.adjust);

            // Shortly display welcome screen
            this.displayAboutModal(1000);

            var assignmentView = new AssignmentView(this.domain);
            var navView = new NavView({model: this.model, el: $(this.$el).find('#navbarContainer')});
            var stockroomView = new StockroomView({el: $(this.$el).find("#stockroomContainer"), domain: this.domain});
            var instrumentPanelView = new InstrumentPanelView({model: this.model, el: $(this.$el).find("#instrumentPanelContainer")});
            this.model.set("assignmentView", assignmentView);
            this.model.set("navView", navView);
            this.model.set("stockroomView", stockroomView);
            this.model.set("instrumentPanelView", instrumentPanelView);
            //this.model.set("userScaleFactor", 1.0);
            if (options && options.vlab) {
                await this.loadAssignmentJSON(options.vlab); // maybe still needs await?
            }

            stockroomView.open();

            this.finishedLoading = true;
            logger.publish("vlabInitialized", {majorVersion: this.majorVersion, minorVersion: this.minorVersion, buildNumber: this.buildNumber, versionString: this.getVersionString()});
        },
        render: function(){
            var dimensions = [$(window).width(), $(window).height()];
            var renderedTemplate = this.template({width: dimensions[0],height: dimensions[1], terms: _.extend({}, lang.app, lang.actions), language: language});
            this.$el.html(renderedTemplate);
            this.adjust();
        },
