        duplicateVessel: function (event) {
            if ($(event.target).hasClass("editItem")) {
                $("#edit").dropdown("toggle");
            }
            var currentWorkbenchCid = this.model.get("currentWorkbenchCid");
            var workbenches = this.model.get("workbenches");
            var workbench = workbenches.get(currentWorkbenchCid);
            var vesselCid = workbench.get("currentVesselCid");
            $("#" + vesselCid).removeClass("selectedVessel");
            var vessel = workbench.get("vesselCollection").get(vesselCid);
            var s = vessel.get("solution");
            var speciesData = s.getSpeciesData();
            var i;
            for (i = 0; i < speciesData.length; i++) {
                speciesData[i].amount = speciesData[i].moles;
                delete speciesData[i].moles;
            }
            var solution = {
                description: s.description,
                kb: s.kb,
                name: s.name,
                species: speciesData,
                vessel: vessel.get("vessel").name,
                volume: s.liquidVolume,
                solidVolume: s.getSolidVolume(),
                temperature: s.getTemperature(),
                insulated: s.insulated
            };
            workbench.addSolution(solution, solution.vessel);
            vesselCid = workbench.get("currentVesselCid");
            $("#" + vesselCid).addClass("selectedVessel");
        },
        vesselThermalProperties: function (event) {
            if ($(event.target).hasClass("editItem")) {
                $("#edit").dropdown("toggle");
            }
            var currentWorkbenchCid = this.model.get("currentWorkbenchCid");
            var workbenchDOM = $("#" + currentWorkbenchCid);
            var workbenches = this.model.get("workbenches");
            var workbench = workbenches.get(currentWorkbenchCid);
            var vesselCid = workbench.get("currentVesselCid");
            var vesselDOM = $("#" + vesselCid);
            vesselDOM.draggable("disable");
            var workbenchViews = this.model.get("workbenchViews");
            var workbenchView = workbenchViews[currentWorkbenchCid];
            var vessel = workbench.get("vesselCollection").get(vesselCid);
            var instrumentPanel = this.model.get("instrumentPanelView");
            var solution = vessel.get("solution");
            var left, top;
            if (vesselDOM.position().left + vesselDOM.width() + 322 > workbenchDOM.width()) {
                left = workbenchDOM.width() - 322;
                if (vesselDOM.position().top > workbenchDOM.height() / 2) {
                    top = vesselDOM.position().top - 106;
                }
                else {
                    top = vesselDOM.position().top + vesselDOM.height();
                }
            }
            else {
                left = vesselDOM.position().left + vesselDOM.width();
                top = vesselDOM.position().top;
            }
            var popover = $("#vesselThermalPropertiesPopover");
            popover.css('display', 'block');
            popover.css('top', top);
            popover.css('left', left);
            var insulated = $("#vesselInsulatedInput");
            insulated.prop("checked", solution.insulated);
            var cancel = $("#cancelThermalButton");
            var temperatureInput = $("#vesselTemperatureInput");
            temperatureInput.focus();
            var warning = $("#inputWarning");
            cancel.unbind('click');
            cancel.one('click', function(){
                vesselDOM.draggable("enable");
                popover.css('display', 'none');
                temperatureInput.val("");
                warning.css("display", "none");
            });
            var button = $("#vesselThermalPropertiesButton");
            button.unbind('click');
            button.on('click', function(){
                if (temperatureInput.val() === "") {
                    vesselDOM.draggable("enable");
                    solution.setInsulated(insulated.is(":checked"));
                    workbenchView.render();
                    instrumentPanel.update(vessel);
                    popover.css('display', 'none');
                    temperatureInput.val("");
                    warning.css("display", "none");
                }
                else {
                    var temperature = parseFloat(temperatureInput.val());
                    if (isNaN(temperature) || temperature <= -273.15) {
                        warning.html("Please enter a valid temperature in degrees Celsius.");
                        warning.css("display", "block");
                    }
                    else {
                        vesselDOM.draggable("enable");
                        solution.setInsulated(insulated.is(":checked"));
                        solution.changeTemperature(temperature + 273.15);
                        solution.findEquilibriumTemp();
                        vessel.set("temperature", solution.getTemperature());
                        var speciesData = solution.getSpeciesData();
                        vessel.set("species", speciesData);
                        vessel.set("pH", solution.getPH());
                        vessel.set("solution", solution);
                        workbenchView.render();
                        instrumentPanel.update(vessel);
                        popover.css('display', 'none');
                        temperatureInput.val("");
                        warning.css("display", "none");
                    }
                }
                var channel = postal.channel("logger");
                channel.publish("solutionChanged", {source: solution, eventType: "INSULATION"});
            });
            temperatureInput.keypress(function(event) {
                if (event.which == 13) {
                    event.preventDefault();
                    if (temperatureInput.val() === "") {
                        vesselDOM.draggable("enable");
                        solution.setInsulated(insulated.is(":checked"));
                        workbenchView.render();
                        instrumentPanel.update(vessel);
                        popover.css('display', 'none');
                        temperatureInput.val("");
                        warning.css("display", "none");
                    }
                    else {
                        var temperature = parseFloat(temperatureInput.val());
                        if (isNaN(temperature) || temperature <= -273.15) {
                            warning.html("Please enter a valid temperature in degrees Celsius.");
                            warning.css("display", "block");
                        }
                        else {
                            vesselDOM.draggable("enable");
                            solution.setInsulated(insulated.is(":checked"));
                            solution.changeTemperature(temperature + 273.15);
                            solution.findEquilibriumTemp();
                            vessel.set("temperature", solution.getTemperature());
                            var speciesData = solution.getSpeciesData();
                            vessel.set("species", speciesData);
                            vessel.set("pH", solution.getPH());
                            vessel.set("solution", solution);
                            workbenchView.render();
                            instrumentPanel.update(vessel);
                            popover.css('display', 'none');
                            temperatureInput.val("");
                            warning.css("display", "none");
                        }
                    }
                    var channel = postal.channel("logger");
                    channel.publish("solutionChanged", {source: solution, eventType: "INSULATION"});
                }
            });
        },
