        updateVessels: function () {
            var workbenchCollection = this.model.get("workbenches");
            var workbenches = workbenchCollection.models;
            //var userScaleFactor = this.model.get("userScaleFactor");
            var i, j;
            for (i = 0; i < workbenches.length; i++) {
                //workbenches[i].set("userScaleFactor", userScaleFactor);
                var vesselCollection = workbenches[i].get("vesselCollection").models;
                for (j = 0; j < vesselCollection.length; j++) {
                    var vessel = vesselCollection[j];
                    //vessel.set('userScaleFactor', userScaleFactor);
                    var solution = vessel.get("solution");
                    if (solution !== null) {
                        solution.findEquilibriumTemp();
                        vessel.set('solution', solution);
                        vessel.set('species', solution.getSpeciesData());
                        vessel.set('pH', solution.getPH());
                        vessel.set('temperature', solution.getTemperature());
                        vessel.set('color', solution.getColor());
                        vessel.set('solidVolume', solution.getSolidVolume());
                        vessel.set('solidColor', solution.getSolidColor());

                    }
                    if (vessel.get("timedTracking")) {
                        vessel.recordProperties();
                    }
                }
                // _.each(workbenches[i].get("toolViews"), function(tool){
                //     tool.render();
                // });
            }
            var currentWorkbenchCid = this.model.get("currentWorkbenchCid");
            var workbench = workbenchCollection.get(currentWorkbenchCid);
            var currentVesselCid = workbench.get("currentVesselCid");
            var vesselCollection = workbench.get("vesselCollection");
            var vessel = vesselCollection.get(currentVesselCid);
            if (vessel) {
                var instrumentPanel = this.model.get("instrumentPanelView");
                instrumentPanel.update(vessel);
            }
            setTimeout(_.bind(this.updateVessels, this), 150);
        },
